import React, { useState } from "react";
import ProposeIdeaForm from "./ProposeIdeaForm";
import { RiCloseCircleLine } from "react-icons/ri";
import { TiEdit } from "react-icons/ti";
import { HowToVoteTwoTone, ThumbDownTwoTone } from "@mui/icons-material";
import moment from 'moment';
import "./share/Share.css"

const ProposeIdea = ({ ideas, completeIdea, removeIdea, updateIdea, post }) => {
  const [edit, setEdit] = useState({
    id: null,
    value: "",
  });

  const [like, setLike] = useState(0);

  let myDate;
  myDate = moment().fromNow();

  const voteHandler = () => {
    if (like === 0) {
      setLike(like + 1);
    } else {
      return false;
    }
  };

  const removeVoteHandler = () => {
    if (like === 0) {
      return false;
    } else {
      setLike(like - 1);
    }
  };

  const submitUpdate = (value) => {
    updateIdea(edit.id, value);
    setEdit({
      id: null,
      value: "",
    });
  };

  if (edit.id) {
    return <ProposeIdeaForm edit={edit} onSubmit={submitUpdate} />;
  }

  return ideas.map((idea, index) => (
    <div
      className={idea.isComplete ? "todo-row complete" : "todo-row"}
      key={index}
    >
      <div className="postTopLeft">
        <img className="postProfileImg" src="/assets/person/1.jpeg" alt="" />
        <span className="postUsername">Alberto</span>
        <span className="postDate">{myDate}</span>
      </div>
      <div key={idea.id} onClick={() => completeIdea(idea.id)}>
        {idea.text}
      </div>
      <div className="icons">
        <RiCloseCircleLine
          onClick={() => removeIdea(idea.id)}
          className="delete-icon"
        />
        <TiEdit
          onClick={() => setEdit({ id: idea.id, value: idea.text })}
          className="edit-icon"
        />
        <div className="postBottomLeft">
          <HowToVoteTwoTone className="likeIcon" onClick={voteHandler} />
          <ThumbDownTwoTone className="likeIcon" onClick={removeVoteHandler} />
          <span className="postLikeCounter">{like} votes</span>
          <span className="postCommentText">0 comments</span>

        </div>
      </div>
    </div>
  ));
};

export default ProposeIdea;
