import { useState} from "react";
import "./Register.css";
import { useHistory } from "react-router-dom";


export default function Register() {
  let history = useHistory();
  const initialValue = {
    username: "",
    email: "",
    password1: "",
    password2: "",
  };
  const [formValues, setFormValues] = useState(initialValue);
  const [formErrors, setFormErrors] = useState({});
  const [isSubmit, setIsSubmit] = useState(false);
  const handleChange = (e) => {
    const { name, value} = e.target;
    setFormValues({...formValues, [name]: value})
  }


  const handleSubmit = (e) => {
    e.preventDefault();
    setFormErrors(validate(formValues))
    setIsSubmit(true)
  }


  const validate = (values) => {
    const errors = {}
    const regex = /^[^\s@]+@[^\s@]+\.[^\s@]{2,}$/i

    if (!values.username) {
      errors.username = "Username is required"
    }

    if (!values.email) {
      errors.email = "Email is required"
    }else if (!regex.test(values.email)) {
      errors.email = "This is not a valid email address"
    }else if (values.password1.length < 4) {
      errors.password1 = "Password must be more than 4 characters"
    }

    if (!values.password1) {
      errors.password1 = "This first password is required"
    }

    if (!values.password2) {
      errors.password2 = "This second password is required"
    }

    if (values.password1 !== values.password2) {
      errors.password2 = "Password must match!"
    }
    return errors
  }
  return (
    <div className="login">
      {Object.keys(formErrors) == 0 && isSubmit ? history.push('/home') : <div></div>}
      <div className="loginWrapper">
        <div className="loginLeft">
          <h3 className="loginLogo">Social Community</h3>
          <span className="loginDesc">Join the Community today</span>
        </div>
        <form onSubmit={handleSubmit}>
        <div className="loginRight">
          <div className="loginBox">
              <p>{ formErrors.username }</p>
            <input
              type="text"
              name="username"
              placeholder="Username"
              className="loginInput"
              value={formValues.username}
              onChange={handleChange}
            />
            <p>{ formErrors.email}</p>
            <input
              type="email"
              name="email"
              placeholder="Email"
              className="loginInput"
              value={formValues.email}
              onChange={handleChange}
            />
            <p>{ formErrors.password1}</p>
            <input
              type="password"
              name="password1"
              placeholder="Password"
              className="loginInput"
              value={formValues.password1}
              onChange={handleChange}
            />
            <p>{ formErrors.password2}</p>
            <input
              type="password"
              name="password2"
              placeholder="Confirm Password"
              className="loginInput"
              value={formValues.password2}
              onChange={handleChange}
            />
            <button
              className="loginButton"
            >
              Sign up
            </button>
            <button
              onClick={() => {
                history.push("/login");
              }}
              className="loginRegisterButton"
            >
              Log Into Account
            </button>
          </div>
        </div>
        </form>
      </div>
    </div>
  );
}
