import { useState } from "react";
import "./Login.css";
import { useHistory } from "react-router-dom";


export default function Login() {
  let history = useHistory();
  const initialValue = {
    email: "",
    password: "",
  };
  const [formValues, setFormValues] = useState(initialValue);
  const [formErrors, setFormErrors] = useState({});
  const [isSubmit, setIsSubmit] = useState(false);
  const handleChange = (e) => {
    const { name, value} = e.target;
    setFormValues({...formValues, [name]: value})
  }


  const handleSubmit = (e) => {
    e.preventDefault();
    setFormErrors(validate(formValues))
    setIsSubmit(true)
  }

  const validate = (values) => {
    const errors = {}
    const regex = /^[^\s@]+@[^\s@]+\.[^\s@]{2,}$/i


    if (!values.email) {
      errors.email = "Email is required"
    }else if (!regex.test(values.email)) {
      errors.email = "This is not a valid email address"
    }else if (values.password.length < 4) {
      errors.password = "Password must be more than 4 characters"
    }

    if (!values.password) {
      errors.password = "Password is required"
    }

    return errors
  }

  return (
    <div className="login">
      {Object.keys(formErrors) == 0 && isSubmit ? history.push('/home') : <div></div>}
      <div className="loginWrapper">
        <div className="loginLeft">
          <h3 className="loginLogo">Social Community</h3>
          <span className="loginDesc">Join the Community today</span>
        </div>
        <form onSubmit={handleSubmit}>
          <div className="loginRight">
            <div className="loginBox">
              <p>{ formErrors.email}</p>
              <input type="email"
               placeholder="Email"
                className="loginInput"
                name="email"
                value={formValues.email}
                onChange={handleChange}
                 />
              <p>{ formErrors.password}</p>
              <input
                type="password"
                placeholder="Password"
                className="loginInput"
                name="password"
                value={formValues.password}
                onChange={handleChange}
              />
              <button
                className="loginButton"
              >
                Login
              </button>
              <span onClick={()=> {history.push("/forgot")}} className="loginForgot">Forgot password?</span>
              <button
                onClick={() => {
                  history.push("/register");
                }}
                className="loginRegisterButton"
              >
                Create a new account
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
}
