import React, { useState } from 'react';
import ProposeIdeaForm from "./ProposeIdeaForm"
import ProposeIdea from "./ProposeIdea";


function ProposeIdeaList() {
  const [ideas, setIdeas] = useState([]);

  const addIdea = idea => {
    if (!idea.text || /^\s*$/.test(idea.text)) {
      return;
    }

    const newIdeas = [idea, ...ideas];

    setIdeas(newIdeas);
    console.log(...ideas);
  };

  const updateIdea = (todoId, newValue) => {
    if (!newValue.text || /^\s*$/.test(newValue.text)) {
      return;
    }

    setIdeas(prev => prev.map(item => (item.id === todoId ? newValue : item)));
  };

  const removeIdea = id => {
    const removedArr = [...ideas].filter(idea => idea.id !== id);

    setIdeas(removedArr);
  };

  const completeIdea = id => {
    let updateIdeas = ideas.map(idea => {
      if (idea.id === id) {
        idea.isComplete = !idea.isComplete;
      }
      return idea;
    });
    setIdeas(updateIdeas);
  };

  return (
    <>
      <ProposeIdeaForm onSubmit={addIdea} />
      <ProposeIdea
        ideas={ideas}
        completeIdea={completeIdea}
        removeIdea={removeIdea}
        updateIdea={updateIdea}
        post
      />
    </>
  );
}

export default ProposeIdeaList;
